.App {
  
  border-style: solid;
  border-width: thin;
  display: flex;
  text-align: center;
  height: fit-content;
}
.left{
  padding: 20px;
  flex: .25;
  background-color: rgb(218, 210, 197);
  padding-top: 50px;
 font-family: calibri;
}
.right{
  flex: .75;
  padding-top: 150px;
}
.avatar{
  margin-left: 20%;
  border-radius: 100%;
}

.top{
  text-align: center;
  position: absolute;
  padding-left: 42.7vw;
 
  
}
.top h2{
  font-family:poppins;
  color: rgb(0, 17, 6);
  font-size: 40px;
  margin-top: -40px;
  font-weight: 400;
}
.top h3{
  font-weight: 370;
  font-size: 26px;
  font-family: poppins;
}

.contact p1{
  margin-left: 3px;
  margin-top: 15px;
  font-size: 14px;
  color: rgb(7, 22, 12);
  font-weight:350;
}
.contact h1{
  font-weight:350;
  font-size: 23px;
}
.contact{
  padding-top: 60%;
}

hr{
  background: linear-gradient(to right, rgb(190, 214, 194),rgb(168, 167, 167), rgb(183, 216, 205));
  height: 1px;
  border-style: hidden;
}


.header-right{
  height: 4vh;
  background-color: rgb(218, 210, 197);
  width: 80%;
  margin-left: 10%;
}
.letter{
  width: 70%;
  margin-left: 15%;

}
.letter p{
  color: rgb(0, 0, 0);
  text-align: justify;
  font-family: calibri;
}
.letter p1{
  float: left;
  text-align: left;
  font-family: calibri;
  color: rgb(16, 29, 26);
}

.date{
  float: right;
